import Dexie from "dexie";

let MyPlugin = {};

MyPlugin.install = function (Vue, options) {
    const db = new Dexie(options.dbname);
    db.version(1).stores({
        projects: '++id, name, &slug, description, *tags, created, updated',
        features: '++id, pid, title, options, description, *scenarios, created, updated'
    });
    db.version(2).stores({
        projects: '++id, &slug, *tags, created, updated',
        features: '++id, pid, created, updated',
        vocabularies: '++id, &pid'
    });
    db.version(3).stores({
        diagrams: '++id, &pid'
    });
    db.version(4).stores({
        tasks: '++id, pid, from, isDone, created, updated'
    });
    db.on('populate', function () {
        db.projects.add({id:1, name:'SML Toolkit', slug:'sml-toolkit', description: 'It is a tool for project management, from gathering requirements to UML-like design charts, also following BDD-style and thinkings in simple modeling language.', tags: ['bdd-style', 'gathering requirements', 'project management', 'uml-like design charts'], created: 1571318371433, updated: 1571318371433});
        db.features.add({id:1, pid:1, title:'Project management', options:{hasInOrderTo: true, hasIShouldBe: true, hasSoThat: true, hasBackground: false}, description:{txtInOrderTo: 'do project management', txtIShouldBe: 'a team leader', txtSoThat: 'I can add, modify, delete and list the projects.'}, background:{given: '', ands:[]}, scenarios:[{title: 'View Projects', isDone: true, taskId:1, isOutline: false, options: {hasGiven: true, hasWhen: true, hasThen: true}, given: {given: 'there exists a project named \'SML Toolkit\'', ands: []}, when: {when: 'I visit the page \'/\'', ands: []}, then: {then: 'I see \'SML Toolkit\' listed in \'Projects\' section.', ands: []}, examples: ''}, {title: 'Choose Project', isDone: false, taskId:2, isOutline: false, options: {hasGiven: true, hasWhen: true, hasThen: true}, given: {given: 'there exists a project named \'SML Toolkit\'', ands: []}, when: {when: 'I select the \'SML Toolkit\' on the \'Projects\' list', ands: []}, then: {then: 'there will be \'SML Toolkit\' appear in the form \'title\' field', ands: [{isBut: false, value: 'also have \'tool\' in \'Auto Suggested Tags\' section'}, {isBut: false, value:'also have \'project management\' in \'Saved Tags\' section'}]}, examples: ''}], created:1571375271588, updated:1571375271588});
        // db.vocabularies.add({id:1, pid:1, nouns:[], verbs:[]});
        db.tasks.add({id:1, pid:1, from: 'scenario', task: 'View Projects', isDone: true, isScheduled: false, isArchived: true, days: 3.5,created:1571375271588, updated:1571375271588});
        db.tasks.add({id:2, pid:1, from: 'scenario', task: 'Choose Projects', isDone: false, isScheduled: false, isArchived: false, days: 3.5,created:1571375271588, updated:1571375271588});
    });
    db.open();
    Vue.prototype.$db = db;
};

export default MyPlugin;

